import * as React from "react"
import Layout from "../components/layout"
import { graphql } from "gatsby"

import PageTitle from "../components/PageTitle"
import Seo from "../components/seo"

import { faPhoneVolume } from "@fortawesome/free-solid-svg-icons/faPhoneVolume"
import { faEnvelopeOpenText } from "@fortawesome/free-solid-svg-icons/faEnvelopeOpenText"
import { faClock } from "@fortawesome/free-solid-svg-icons/faClock"
import { faMessage } from "@fortawesome/free-solid-svg-icons"
import { FaIcon } from "../components/FaIcon"
import Title from "../components/Title"
import QuoteBlock from "../components/QuoteBlock"


const fa_icons = [faPhoneVolume, faMessage, faClock, faEnvelopeOpenText]

export const Head = () => <Seo title="Información de contacto" />

const ContactPage = ({ data }) => (
  <Layout>
    <PageTitle title="Contacto" />

    <section className="construct-contact-page-area section_100">
      <div className="container">
        <div className="row">
          <div className="col-md-10">
            <div className="construct-contact-desc">
              <Title
                section={data.allPagesJson.nodes[0].block1.title}
                title={data.allPagesJson.nodes[0].block1.category}
              />
            </div>
          </div>
        </div>
        <div className="construct-contact-form-bottom">
          <div className="row">
            <div className="col-md-5">
              <div className="contact-form-left">
                {data.contactInfo.edges.map(
                  (item, i) => {
                    return (
                      <div key={"ci" + i} className="single-contact-left">
                        <div className="contact-icon">

                          <FaIcon
                            icon={fa_icons[i]}
                            fill={"#fbb908"}
                            className="icon text-center"
                            width={24}
                            height={37}
                          />
                        </div>
                        <div className="contact-text">
                          <h4>{item.node.description}</h4>
                          <p dangerouslySetInnerHTML={{
                            __html: item.node.data,
                          }}></p>
                          <br />
                        </div>
                      </div>
                    )
                  }
                )}

              </div>
            </div>
            <div className="col-md-7">
              <QuoteBlock classCss="construct-contact-form-right" />

            </div>
          </div>
        </div>
      </div>
    </section>
  </Layout>
)

export const query = graphql`
  query {
     contactInfo: allHeaderContactInformationJson {
    edges {
      node {
        description
        data
      }
    }
  },
    allPagesJson(filter: { idPage: { eq: "contacto" } }) {
      nodes {
        idPage
        block1 {
          category
          title
        }
        block2 {
          category
          title
        }
      }
    }
  }
`

export default ContactPage
