import React, { useState } from 'react';

const QuoteBlock = ({ classCss }) => {

    const [dataForm, setDataForm] = useState({});

    const handleChange = (event) => setDataForm({ ...dataForm, [event.target.id]: event.target.value });

    const handleSubmit = (event) => {
        event.preventDefault();

        const scriptURL =
            "/macros/s/AKfycbzfhVIHoIHqvyor8DlDtlEAoHAuUggah3gAYwMz8gMaK8j6b8e6vMVt65F1_VjfNGi7/exec";

        const formData = new FormData();
        Object.keys(dataForm).forEach(step => {

            formData.append(step, dataForm[step]);

        });

        fetch(scriptURL, {
            method: "POST",
            body: formData
        })
            .then(() => {
                console.log("Success!");
            })
            .catch((error) => {
                console.error("Error!", error.message);
            });
    }


    return (
        <div className={classCss}>
            <form method="POST" onSubmit={handleSubmit}>
                <div className="row">
                    <div className="col-md-6">
                        <p>
                            <label htmlFor="name" >Nombre / Razón Social</label>
                            <input type="text" onChange={handleChange} id="name" name="name" placeholder="Nombre / Razón Social..." />
                        </p>
                    </div>
                    <div className="col-md-6">
                        <p>
                            <label htmlFor="email" >Email</label>
                            <input type="email" onChange={handleChange} id="email" name="email" placeholder="Email..." />
                        </p>
                    </div>
                </div>
                <div className="row">
                    <div className="col-md-6">
                        <p>
                            <label htmlFor="phone" >Teléfono</label>
                            <input type="text" onChange={handleChange} id="phone" name="phone" placeholder="Teléfono..." />
                        </p>
                    </div>

                </div>
                <div className="row">
                    <div className="col-md-12">
                        <p>
                            <label htmlFor="msg" >¿Cómo podemos ayudarte?</label>
                            <textarea id="msg" onChange={handleChange} name="msg" placeholder="Mensaje..." ></textarea>
                        </p>
                    </div>
                </div>
                <div className="row">
                    <div className="col-md-12">
                        <p>
                            <button type="submit" >solicitar presupuesto</button>
                        </p>
                    </div>
                </div>
            </form>
        </div>
    )
}

export default QuoteBlock